.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 40px;
  padding: 14px 16px;
  color: #292929;

  background-color: #F8F8FA;

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7E7E83;
  }

  &:hover {
    border-color: #D5D7DE;
  }

  &:focus {
    border-color: #292929;
    outline: none;
  }

  &--textaarea {
    height: 148px;
    font-family: 'Poppins';
    resize: none;
  }
}